

.App {
  text-align: center;
  height: 100vh;
  width: inherit;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}

.innerApp{
  border: 2px double rgb(29, 207, 243);
  width: 50%;
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background-color: rgb(206, 236, 243);
}
.input{
   font-size: 20px;
   width: 50%;
   border-radius: 5px;
   border: 2px dotted rgb(229, 35, 239);
   margin: 5px;
}
.input:focus{
   outline: none;
}
.btn{
   font-size: 20px;
   border: 1px solid rgb(22, 202, 248);
   width: 30%;
   margin: 10px;
   background: rgb(22, 90, 248);
   height: 50px;
}
.btn:hover{
  border: 1px solid rgb(47, 253, 194);
  background:rgb(103, 240, 62);
  border-radius: 10px;
}
.recieveMessage{
  border: 2px double rgb(68, 222, 21);
  width: 50%;
  height: 40%;
  border-radius: 10px;
  background: rgb(218, 246, 209);
}
